<template>
  <div>
    <div v-if="lists.length" id="buycar">
      <table>
        <tr>
          <th>选择</th>

          <th>商品信息</th>

          <th>款式名称</th>

          <th>尺码/颜色</th>

          <th>品牌价</th>

          <th>数量</th>

          <th>折扣</th>

          <th>总金额</th>

          <th>操作</th>
        </tr>

        <tr v-for="list in lists" :key="list.id">
          <td>
            <el-checkbox
              v-model="seletedArrCloth"
              :label="list.id"
              @change="list.checked = !list.checked"
            ></el-checkbox>
          </td>

          <td>
            <img :src="list.image" />
          </td>

          <td>
            <span>{{list.name}}</span>
          </td>

          <td>{{list.size}}/{{list.color}}</td>

          <td>{{list.price}}</td>

          <td class="col">
            <span @click="addCount(list)" class="span">+</span>
            {{list.count}}
            <span @click="CutCount(list)" class="span">-</span>
          </td>

          <td>{{list.active}}</td>

          <td>{{list.price*list.count}}</td>

          <td>
            <img class="delIcon" @click="delIcon(list)" src="../../static/image/delIcon.png" />
          </td>
        </tr>
      </table>
    </div>

    <div v-else>购物车空空如也，快去逛逛吧^-^</div>

    <Footer
      id="Footer"
      :lists="lists"
      :totalPrice="totalPrice"
      :totalCount="totalCount"
      :seletedArrCloth="seletedArrCloth"
      @funct="getMessage"
      :allchecked="allchecked"
    ></Footer>
  </div>
